.table {
    width: 100%;
    margin-bottom: 20px;
    border: 1px solid #dddddd;
    border-collapse: collapse;
  }
  .table_th {
    font-weight: bold;
    padding: 5px;
    background: #efefef28;
    border: 1px solid #dddddd;
  }
  .table_td {
    border: 1px solid #dddddd;
    padding: 5px;
  }